}
.events__carousel .event__name {
  font-family: var(--lato);
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 1.8rem;
  text-align: center;
  text-transform: capitalize;
  color: rgba(var(--text-color), 1);
}
@media (min-width: 1024px) {
  .events__carousel .event__name {
    font-size: 1.8rem;
    line-height: 2rem;
  }
}
.events__carousel .event__day {
  text-align: center;
  text-transform: capitalize;
}
.events__carousel .event__link {
  width: fit-content;
  padding-bottom: 3px;
  border-bottom: 1px solid rgba(var(--primary-complement), 1);
  font-family: var(--lato);
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 2rem;
  text-align: center;
