  }
}
@media (min-width: 1440px) {
  .events .cta-banner {
    padding: 10rem 5rem 8rem 5rem;
  }
}
.events .cta-banner .banner-content {
  flex: 1 0 15rem;
}
@media (min-width: 1440px) {
  .events .cta-banner .banner-content {
    flex-basis: 20rem;
  }
}
.events .cta-banner .banner-content .cta-tagline {
  margin-bottom: 10px;
  font-family: var(--lato);
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 1.8rem;
  text-transform: uppercase;
  color: rgba(var(--text-color), 1);
}
@media (min-width: 1440px) {
  .events .cta-banner .banner-content .cta-tagline {
    margin-bottom: 20px;
    font-size: 2rem;
    line-height: 2.2rem;
  }
}
.events .cta-banner .banner-content .cta-title {
  font-family: var(--lato);
  font-weight: 600;
  font-size: 1.8rem;
  line-height: 2.2rem;
  color: rgba(var(--primary-color));
}
@media (min-width: 1440px) {
  .events .cta-banner .banner-content .cta-title {
    font-size: 2.2rem;
    line-height: 2.4rem;
  }
}
.events .cta-banner .btn-primary {
  flex: 1 0 5rem;
}
@media (min-width: 1440px) {
  .events .cta-banner .btn-primary {
    flex-grow: 0.3;
  }
}

.pictures-carousel {
  margin-bottom: 50px;
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  color: rgba(var(--secondary-color), 0.6);
}
.pictures-carousel .section-title {
  margin-bottom: 3rem;
}

.pic_carousel {
  width: 100%;
  height: 100%;
  margin-bottom: 0;
  padding: 0;
}

.pic-slide {
  padding: 0;
  position: relative;
}
.pic-slide svg {
  display: none;
}
.pic-slide:hover img {
  transform: scale(1.2);
}
.pic-slide:hover::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background-color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}
.pic-slide:hover svg {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  display: block;
  cursor: pointer;
}
.pic-slide:hover svg path {
  fill: #fff;
}

.pic-slide .slide-picture {
  width: 100%;
  height: 250px;
  overflow: hidden;
  display: block;
  cursor: pointer;
}

.pic-slide .slide-picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  transition: 0.2s ease-in-out;
}

.swiper-button-next {
  padding: 40px 20px;
  color: rgba(var(--primary-complement), 1);
}
.swiper-button-next:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.swiper-button-prev {
  padding: 40px 20px;
  color: rgba(var(--primary-complement), 1);
}
.swiper-button-prev:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.lightbox {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: none;
  background-color: transparent;
  justify-content: center;
  align-items: center;
}
.lightbox .close {
  position: absolute;
  top: 80px;
  right: 80px;
  display: block;
  background-color: transparent;
  border: none;
  font-size: 30px;
  line-height: 1;
  color: #fff;
  opacity: 0;
  transition: 0.5s cubic-bezier(0.17, 0.66, 0.34, 0.98);
}
.lightbox__wrapper {
  max-width: 700px;
  width: 0;
  max-height: 500px;
  height: 500px;
  min-height: fit-content;
  padding: 60px 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.5s cubic-bezier(0.17, 0.66, 0.34, 0.98);
}
.lightbox__inner {
  width: 100%;
  height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.lightbox__content {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.17, 0.66, 0.34, 0.98) 0.6s;
}
.lightbox__content p {
  color: #fff;
  font-family: var(--lato);
  font-size: 2rem;
  font-weight: 500;
  line-height: 28px;
}
.lightbox.open {
  background-color: rgba(0, 0, 0, 0.6);
}
.lightbox.open .close {
  opacity: 1;
}
.lightbox.open .lightbox__wrapper {
  width: 80%;
  min-width: 250px;
  opacity: 1;
}
.lightbox.open .lightbox__wrapper picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.lightbox.open .lightbox__wrapper picture img {
  object-fit: contain;
}
.lightbox.open .lightbox__content {
  opacity: 1;
}